.effect-container {
  width: 450px;
}
.effect-container .click {
  color: #1890FF;
  cursor: pointer;
  float: right;
}
.effect-container .highlight {
  color: #1890FF;
}
.effect-container .header {
  background: #FFF4EB;
  border: 1px solid #FFDFC5;
  padding: 12px;
  margin: 12px 0;
}
.effect-container .body {
  display: none;
  overflow: hidden;
  background: red;
}
.effect-container .body.show {
  display: block;
}
